.section {
  margin-top: 20vh;
}

h2 {
  text-align: center;
  font-size: 45px;
}

.skills {
  max-width: 1070px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  .skill {
    background-color: #283852;
    margin: 20px;
    border-radius: 20px;
    border-style: solid;
    border-width: 2px;
    border-color: #3d4a5f;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    text-decoration: none;
    transition: transform 0.3s, box-shadow 0.3s;
    max-height: 150px;
    max-width: 150px;
    width: 100%;
  
    img {
      width: 50%;
      filter: invert(100%);
      display: block;
      margin: auto;
    }
    
    p {
      font-weight: bold;
      font-size: 20px;
      text-align: center;
      color: #CBD5E1;
    }
    @media screen and (max-width: 324px) {
      p {
        font-size: 6cqmin;
      }
    }
    
  }
  .skill:hover {
    transform: translateY(-5px);
  }
  @media screen and (max-width: 447px) {
    .skill {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      margin: 0;
      padding: 0;
      margin: 10px;
      padding-top: 20px;
      padding-bottom: 10px;
      max-width: 40vw;
    }
  }
  @media screen and (max-width: 339px) {
    .skill {
      max-width: 90%;

      img {
        max-width: 75px;
      }
    }
  }
}